import { h, Fragment } from "preact";
import { Link } from "preact-router";

import { Card, CardList } from "src/components/CardList";
import { jobsSortedByStartDate } from "src/data/jobs";
import { RouteAwareProps } from "src/declarations";
import Footer from "src/components/Footer";
import Section from "src/components/Section";
import { skills } from "./skills";

const renderedJobs = jobsSortedByStartDate.map(
  ([id, { name, position, startDate, endDate }]) => (
    <Card
      Wrapper={Link}
      title={`${name} - ${position}`}
      href={`/expertise/${id}`}
      key={id}
    >
      <h3>{name}</h3>
      <h4>
        <b>Position:</b> {position}
      </h4>
      <div>
        <h5>
          <b>
            Started:{" "}
            {startDate
              ? `${startDate.toLocaleString("default", {
                  month: "long",
                  year: "numeric",
                })}`
              : "-"}
          </b>
        </h5>
        <h5>
          <b>
            {endDate
              ? `Ended: ${endDate.toLocaleString("default", {
                  month: "long",
                  year: "numeric",
                })}`
              : "Ongoing"}
          </b>
        </h5>
      </div>
    </Card>
  )
);

export default function Expertise(_: Readonly<RouteAwareProps>) {
  return (
    <>
      <Section>
        <h1>Expertise</h1>
        <hr />
        <h2>Carreer summary</h2>
        <p></p>
        <CardList>{renderedJobs}</CardList>
        <h2>Skills</h2>
        <p>{skills}</p>
      </Section>
      <Footer />
    </>
  );
}
